<script>
import {
  fetchGetInstanceList,
  fetchGetUpfLoad,
  fetchGetUpfBusinessVolume,
  fetchQueryUPFBaseInfo,
  fetchQueryDPInfo,
  fetchQueryN9Info,
  fetchQueryN6Info,
  fetchQueryN3Info,
} from "@/api/demo/index.js";
import layout from "../../../layout/header-aside/layout.vue";
import instenceSelect from '../compoents/instenceSelect.vue';
export default {
  components: { layout, instenceSelect },
  name: "upfCount",
  data() {
    return {
      instanceValue: "", //当前选中的实例 upf.starsine.001
      tableList: [], //业务量
      loading: false,
      baseinfoForm: {},
      dpinfoForm: {},
      n9infoForm: {},
      n6infoForm: {},
      n3infoForm: {},
    };
  },
  created() {
    // this.getServiceInstance();
  },
  mounted() {},
  methods: {
    // 选择实例
    changeInstance(val) {
      if (!val) {
        return;
      }
      this.instanceValue = val
      this.loading = true; //显示loading
      this.getUpfWareLoad();
      this.getUpfSessionsload();
      this.getUpfBaseInfo();
      this.getDpInfo();
      this.getN9Info();
      this.getN6Info();
      this.getN3Info();
    },
    //获取实例下的负载
    getUpfWareLoad() {
      fetchGetUpfLoad({
        request_type: "hardware load",
        instance_id: this.instanceValue,
        sequence_no: 1000,
      })
        .then((response) => {
          this.loading = false;
          console.log("response:", response);
          this.drawLine(1, "CPU负载值", [
            { value: response.cpu_load, name: "当前" },
            { value: 100 - response.cpu_load, name: "剩余" },
          ]);
          this.drawLine(2, "内存负载值", [
            { value: response.mem_load, name: "当前" },
            { value: 100 - response.mem_load, name: "剩余" },
          ]);
          this.drawLine(3, "硬盘负载值", [
            { value: response.hd_load, name: "当前" },
            { value: 100 - response.hd_load, name: "剩余" },
          ]);
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
          this.loading = false;
        });
    },
    // 饼状图
    drawLine(type, title, typeData) {
      // 初始化echarts实例
      let myChart = null;
      if (type === 1) {
        myChart = this.$echarts.init(document.getElementById("myCpuChart"));
      } else if (type === 2) {
        myChart = this.$echarts.init(document.getElementById("myMemChart"));
      } else {
        myChart = this.$echarts.init(document.getElementById("myHdChart"));
      }
      // 绘制图表
      myChart.setOption({
        title: { text: title, left: "center" },
        tooltip: {
          trigger: "item",
        },
        legend: {
          orient: "vertical",
          bottom: "left",
        },
        series: [
          {
            name: "访问来源",
            type: "pie",
            radius: "50%",
            data: typeData,
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: "rgba(0, 0, 0, 0.5)",
              },
            },
          },
        ],
      });
    },
    //获取实例下的业务量
    getUpfSessionsload() {
      let data = {
        request_type: "cpe configuration get",
        instance_id: this.instanceValue,
        sequence_no: 1000,
      };
      fetchGetUpfBusinessVolume(data)
        .then((response) => {
          this.loading = false;
          this.tableList = response;
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
          this.loading = false;
        });
    },

    //获取网元基本信息
    getUpfBaseInfo() {
      let data = {
        request_type: "base info",
        instance_id: this.instanceValue,
        sequence_no: 3003,
      };
      fetchQueryUPFBaseInfo(data)
        .then((response) => {
          this.loading = false;
          this.baseinfoForm = response;
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
          this.loading = false;
        });
    },

    //获取网元基本信息
    getDpInfo() {
      let data = {
        request_type: "dp info",
        instance_id: this.instanceValue,
        sequence_no: 3004,
      };
      fetchQueryDPInfo(data)
        .then((response) => {
          this.loading = false;
          this.dpinfoForm = response;
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
          this.loading = false;
        });
    },

    //获取n9信息
    getN9Info() {
      let data = {
        request_type: "n9 info",
        instance_id: this.instanceValue,
        sequence_no: 3005,
      };
      fetchQueryN9Info(data)
        .then((response) => {
          this.loading = false;
          this.n9infoForm = response;
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
          this.loading = false;
        });
    },

    //获取n6信息
    getN6Info() {
      let data = {
        request_type: "n6 info",
        instance_id: this.instanceValue,
        sequence_no: 3006,
      };
      fetchQueryN6Info(data)
        .then((response) => {
          this.loading = false;
          this.n6infoForm = response;
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
          this.loading = false;
        });
    },

    //获取n3信息
    getN3Info() {
      let data = {
        request_type: "n3 info",
        instance_id: this.instanceValue,
        sequence_no: 3007,
      };
      fetchQueryN3Info(data)
        .then((response) => {
          this.loading = false;
          this.n3infoForm = response;
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
          this.loading = false;
        });
    },
  },
};
</script>
